import type { Metadata } from 'next';
import { Inter, Roboto } from 'next/font/google';
import './globals.css';

const inter = Inter({ 
  subsets: ['latin'],
  variable: '--font-inter',
  display: 'swap',
});

const roboto = Roboto({ 
  weight: ['300', '400', '500', '700'],
  subsets: ['latin'],
  variable: '--font-roboto',
  display: 'swap',
});

export const metadata: Metadata = {
  title: 'Ricardo Team - Personal Trainers | Transforme Seu Corpo e Mente',
  description: 'Transforme sua vida com os melhores personal trainers. Treinos personalizados, resultados garantidos. Mais de 500 clientes transformados e 98% de satisfação.',
  keywords: 'personal trainer, academia, fitness, emagrecimento, musculação, treino funcional, São Paulo',
  authors: [{ name: 'Ricardo Team' }],
  creator: 'Ricardo Team',
  publisher: 'Ricardo Team',
  formatDetection: {
    email: false,
    address: false,
    telephone: false,
  },
  metadataBase: new URL('https://ricardoteam.com'),
  alternates: {
    canonical: '/',
  },
  openGraph: {
    title: 'Ricardo Team - Personal Trainers | Transforme Seu Corpo e Mente',
    description: 'Transforme sua vida com os melhores personal trainers. Treinos personalizados, resultados garantidos.',
    url: 'https://ricardoteam.com',
    siteName: 'Ricardo Team',
    images: [
      {
        url: '/og-image.jpg',
        width: 1200,
        height: 630,
        alt: 'Ricardo Team - Personal Trainers',
      },
    ],
    locale: 'pt_BR',
    type: 'website',
  },
  twitter: {
    card: 'summary_large_image',
    title: 'Ricardo Team - Personal Trainers',
    description: 'Transforme sua vida com os melhores personal trainers. Treinos personalizados, resultados garantidos.',
    images: ['/og-image.jpg'],
  },
  robots: {
    index: true,
    follow: true,
    googleBot: {
      index: true,
      follow: true,
      'max-video-preview': -1,
      'max-image-preview': 'large',
      'max-snippet': -1,
    },
  },
  verification: {
    google: 'your-google-verification-code',
  },
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="pt-BR" className={`${inter.variable} ${roboto.variable}`}>
      <head>
        <link rel="icon" href="/favicon.ico" />
        <link rel="apple-touch-icon" sizes="180x180" href="/apple-touch-icon.png" />
        <link rel="icon" type="image/png" sizes="32x32" href="/favicon-32x32.png" />
        <link rel="icon" type="image/png" sizes="16x16" href="/favicon-16x16.png" />
        <link rel="manifest" href="/site.webmanifest" />
        <meta name="theme-color" content="#FF6B35" />
        <meta name="viewport" content="width=device-width, initial-scale=1, maximum-scale=5" />
      </head>
      <body className="font-roboto antialiased">
        {children}
      </body>
    </html>
  );
}